import React from "react";
import axios from "axios";
import Movie from "./Movie";
import "./App.js";

class App extends React.Component {
  state = {
    isLoading: true,
    movies: []
  }

  getMovies = async () => {
    let movies = await axios.get( "https://yts.lt/api/v2/list_movies.json?sory_by=raiting" );
    movies = movies.data.data.movies;
    this.setState(
      {
        isLoading: false,
        movies: movies
      }
    );
    console.log(movies);
  }

  componentDidMount() {
    this.getMovies();
  }

  renderMovie = () => {
    const isLoading = this.state.isLoading;
    const movies = this.state.movies;
    return isLoading ? 
      (
        <div className="loader">
          <span className="loader__text">Loading....</span>
        </div>
      ) : 
      (
        <div className="movies">
        {movies.map(movie => {
          return (
            <Movie
              key={movie.id}
              id={movie.id}
              year={movie.year}
              title={movie.title}
              summary={movie.summary}
              poster={movie.medium_cover_image}
              genres={movie.genres}
            />
          )
        })}
        </div>
      )
  }

  render() {
    return (
      <section className="container">{this.renderMovie()}</section>
    );
  }
}

export default App;